import { memo } from "react";

function Lights() {
  return (
    <group>
      <ambientLight intensity={0.1} />
      <directionalLight
        intensity={0.5}
        position={[51, 51, 51]}
        castShadow
        shadow-mapSize-height={1024}
        shadow-mapSize-width={1024}
      />
    </group>
  );
}

export default memo(Lights);
