import create from "zustand";
import { useCallback } from "react";
import { useSpring } from "react-spring-three";
import { Intro, Loading, Main, Finished } from "../components/UX/Sections";
import { USDZExporter } from "three/examples/jsm/exporters/USDZExporter.js";

/////////////////////////////////////////////////////////////////////////////////////
// Default Variables
/////////////////////////////////////////////////////////////////////////////////////
const debug = 1;
const showLeva = 1;
const version = 0.7;

const sectionObj = {
  Intro: <Intro />,
  Loading: <Loading />,
  Main: <Main />,
  Finished: <Finished />
};
const sectionKeys = Object.keys(sectionObj);
const sectionKey = sectionKeys[0];
const section = sectionObj[sectionKey];
/////////////////////////////////////////////////////////////////////////////////////
// Store
/////////////////////////////////////////////////////////////////////////////////////
export const useApp = create((set, get) => ({
  /////////////////////////////////////////////////////////////////////////////////////
  // Init
  /////////////////////////////////////////////////////////////////////////////////////
  init: () => {
    console.log("init");
    get().ARSupportCheck();
    get().debugMode(true);
  },
  showLeva,
  debugMode: () => {
    // stops console logs
    if (!debug) {
      set((state) => ({ showLeva: 0 }));
      if (!window.console) window.console = {};
      var methods = ["log", "debug", "warn", "info"];
      for (var i = 0; i < methods.length; i++) {
        console[methods[i]] = function () {};
      }
    } else {
      document.getElementById("debug")?.remove();
      var elem = document.createElement("div");
      elem.id = "debug";
      elem.innerHTML = version;
      elem.onclick = () => set((state) => ({ showLeva: !get().showLeva }));
      elem.style.cssText = `
          cursor: pointer;
          color:white;
          position:absolute;
          top:5px;
          left:5px;
          height:45px;
          width:45px;
          opacity:0.3;
          z-index:1000;
          background:#000;
          display: flex;
          align-items: center;
          justify-content: center;
          user-select: none;  
          border-radius: 15px;
      `;
      document.body.appendChild(elem);
    }
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // HUD Navigation & Phases
  /////////////////////////////////////////////////////////////////////////////////////
  section,
  sectionObj,
  sectionKey,
  sectionKeys,
  setSection: (sectionKey) => {
    const section = get().sectionObj[sectionKey];
    set((state) => ({ section, sectionKey }));
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // React-Three-Fiber
  /////////////////////////////////////////////////////////////////////////////////////
  camera: null,
  gl: null,
  scene: null,
  setGL: (gl) => {
    console.log(`GL Set:${gl}`);
    set((state) => ({ gl }));
  },
  setScene: (scene) => {
    console.log(`Scene Set:${scene}`);
    set((state) => ({ scene }));
  },
  setCamera: (camera) => {
    console.log(`Camera Set:${camera}`);
    set((state) => ({ camera }));
  },
  controls: null,
  setControls: (controls) => {
    console.log(`Controls Set:${controls}`);
    set((state) => ({ controls }));
  },
  controlsEnabled: true,
  CameraTo: (cameraPos, lookAt, zoom) => {
    useSpring({
      newZoom: zoom,
      newPos: cameraPos,
      newLookAt: lookAt,
      enabled: true,
      onRest: ({ enabled }) => {
        set((state) => ({ controlsEnabled: true }));
      },
      onStart: ({ enabled }) => {
        set((state) => ({ controlsEnabled: true }));
      },
      onChange: ({ newPos, newZoom }) => {
        get().camera.current.fov = newZoom;
        get().camera.current.position.set(newPos[0], newPos[1], newPos[2]);
      }
    });
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // AR Session
  /////////////////////////////////////////////////////////////////////////////////////
  ARSupport: false,
  ARSupportCheck: async () => {
    const ARSupport =
      navigator.xr &&
      navigator.xr.isSessionSupported &&
      (await navigator.xr.isSessionSupported("immersive-ar"));
    console.log("ARSupportCheck: " + ARSupport);
    set((state) => ({ ARSupport }));
  },
  ARSession: null,
  ARStarted: async (session) => {
    console.log("ARStarted" + session);
    const gl = get().gl;
    //gl.xr.setReferenceSpaceType("local");
    await gl.xr.setSession(session);
    set((state) => ({ ARSession: session }));
  },
  AREnded: () => {
    console.log("AREnded");
  },
  ARActivate: async () => {
    console.log("ARActivate");
    try {
      const ARSession = await navigator.xr
        .requestSession("immersive-ar", {
          requiredFeatures: ["hit-test", "dom-overlay"],
          domOverlay: { root: document.body }
        })
        .then((session) => get().ARStarted(session));
    } catch (e) {
      console.log(e);
    }
  },
  BtnEnterAR: () => {
    return useCallback(() => get().ARActivate(), []);
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // USDZ Exporter
  /////////////////////////////////////////////////////////////////////////////////////
  USDZLink: null,
  SetUSDZ: async (scene) => {
    console.log("SetUSDZ");
    const exporter = new USDZExporter();
    const arraybuffer = await exporter.parse(scene);
    const blob = new Blob([arraybuffer], { type: "application/octet-stream" });

    const USDZLink = document.createElement("a");
    USDZLink.appendChild(document.createElement("img"));
    USDZLink.rel = "ar";
    USDZLink.href = URL.createObjectURL(blob);
    USDZLink.download = "asset.usdz";

    set((state) => ({ USDZLink }));
  },
  BtnUSDZ: () => {
    return useCallback(() => get().USDZLink.click(), []);
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // Functions
  /////////////////////////////////////////////////////////////////////////////////////
  BtnCancel: () => {
    return useCallback(() => console.log("canceled action"), []);
  },
  BtnTakePhoto: () => {
    return useCallback(() => console.log("Take Photo"), []);
  },
  BtnReset: () => {
    return useCallback(() => console.log("reset"), []);
  },
  BtnSave: () => {
    return useCallback(() => console.log("Save Photo"), []);
  },
  BtnExperiance: (expVar) => {
    return useCallback(() => {
      console.log("Experiance Chosen: ", expVar);
    }, [expVar]);
  },
  BtnGoTo: (goToOverlay) => {
    return useCallback(() => {
      // if passed variable isn't in phases show available phases
      if (!sectionObj[goToOverlay]) {
        console.log(`${goToOverlay} is not in phases`);
        console.log(get().sectionKeys);
        return;
      }
      // set phase
      console.log("Going To:", goToOverlay);
      get().setSection(goToOverlay);
    }, [goToOverlay]);
  }
}));
