import { Leva, button, useControls } from "leva";
import { useApp } from "./useApp";

export default function AddLeva() {
  const sectionKeys = useApp((state) => state.sectionKeys);
  const BtnGoTo = useApp((state) => state.BtnGoTo);
  const sections = {};
  sectionKeys.map((k) => (sections[k] = button(BtnGoTo(k))));
  useControls("Go To Section", sections, { collapsed: true });

  const BtnUSDZ = useApp((state) => state.BtnUSDZ);
  const BtnEnterAR = useApp((state) => state.BtnEnterAR);
  useControls(
    "AR",
    {
      "Apple / USDZ": button(BtnUSDZ()),
      WebAR: button(BtnEnterAR())
    },
    { collapsed: true }
  );

  const showLeva = useApp((state) => state.showLeva);
  return <Leva collapsed hidden={!showLeva} />;
}
