import React, { useRef, memo, useEffect } from "react";
import { OrbitControls, PerspectiveCamera } from "@react-three/drei";
import { useThree } from "@react-three/fiber";
import { useApp } from "../../utility/useApp";
import { useControls } from "leva";

const numberRange = (value) => {
  return {
    value: value,
    min: -25,
    max: 50,
    step: 0.001
  };
};

function Controls() {
  const { x, y, z, fov } = useControls(
    "Camera",
    {
      x: numberRange(2),
      y: numberRange(1),
      z: numberRange(1),
      fov: numberRange(25)
    },
    { collapsed: true }
  );

  const myCamera = useRef();
  const myControls = useRef();
  const setControls = useApp((state) => state.setControls);
  useEffect(() => setControls(myControls), [myControls, setControls]);

  const { gl, scene, camera } = useThree();

  const setGL = useApp((state) => state.setGL);
  useEffect(() => setGL(gl), [gl, setGL]);

  const setScene = useApp((state) => state.setScene);
  useEffect(() => setScene(scene), [scene, setScene]);

  const setCamera = useApp((state) => state.setCamera);
  useEffect(() => setCamera(camera), [camera, setCamera]);

  return (
    <>
      <PerspectiveCamera
        makeDefault
        ref={myCamera}
        position={[x, y, z]}
        fov={fov}
      />
      <OrbitControls ref={myControls} camera={myCamera.current} />
    </>
  );
}
export default memo(Controls);
